import React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import "../styles/main-privacy.css"

export default function PrivacyPolicy() {
  const description = `中小企業のIT相談ならホットソウルにお任せください！当サイトのプライバシーポリシーです。| 中小企業のデジタル化・IT活用サポート HOTSOUL`
  const pageUrl = `https://www.hotsoul.jp/privacy-policy/`

  return (
    <Layout>
      <Seo title="プライバシーポリシー" description={description} pageUrl={pageUrl} />
      <section class="wrap privacy">
        <h2>プライバシーポリシー</h2>
        <div class="privacy-inner">
          <dl>
            <dt>1. 個人情報の定義</dt>
            <dd>
              <p>
                本プライバシーポリシーにおける、「個人情報」とは、生存する個人に関する情報であって、
                当該情報に含まれる氏名、生年月日その他の記述等により特定の個人を識別できるものをいいます。
              </p>
            </dd>
          </dl>
          <dl>
            <dt>2. 個人情報の取得、Cookie情報の取得</dt>
            <dd>
              <p>
                当社は、利用目的の達成に必要な範囲で、個人情報を適正に取得し、不正の手段により取得することはありません。
              </p>
              <p>
                また当サイトでは、サイトの利用状況を把握するためにGoogle 社のサービスであるGoogle Analyticsを利用しています。
                当サイトが発行するCookieをもとにGoogle社がお客様の当サイトへの訪問履歴を収集、記録、分析します。
                Google Analyticsにより収集、記録、分析されたお客様の情報には、特定の個人を識別する情報は一切含まれません。
                またそれらの情報は、Google社により同社のプライバシーポリシーに基づいて管理されます。
                <br />
                お客様は、ブラウザのアドオン設定でGoogle Analyticsを無効にすることにより、お客様情報の収集を停止することも可能です。
                <br/>
                これらの利用規約・プライバシーポリシーについてはGoogle社のサイトをご確認ください。
              </p>
              <p>
                <a href="https://policies.google.com/technologies/partner-sites?hl=ja" target="_blank" rel="noopener noreferrer">
                  GOOGLEのサービスを使用するサイトやアプリから収集した情報の GOOGLE による使用
                </a>
                <br/>
                <a href="https://marketingplatform.google.com/about/analytics/terms/jp/" target="_blank" rel="noopener noreferrer">
                  Googleアナリティクス利用規約
                </a>
                <br/>
                <a href="https://tools.google.com/dlpage/gaoptout?hl=ja" target="_blank" rel="noopener noreferrer">
                  Google アナリティクスオプトアウト アドオン
                </a>
              </p>
              <p>
                なお当サイトでは、「Cookieを拒否」ボタンが押下された場合、以降の訪問履歴収集を停止します。
              </p>
            </dd>
          </dl>
          <dl>
            <dt>3. 個人情報の利用目的</dt>
            <dd>
              <p>
                当社は、取得した個人情報を以下の目的で利用します。
              </p>
              <p>
                ①当社サービスのおける関連するアフターサービスや新商品サービスに関する情報の通知
                <br/>
                ②当社サービスに関するお問い合わせ等への対応
                <br/>
                ③当社サービスに関する規約等の変更等の通知
              </p>
            </dd>
          </dl>
          <dl>
            <dt>4. 個人情報の第三者提供</dt>
            <dd>
              <p>
                当社は、法令に定められた場合を除き、あらかじめ利用者の同意を得ないで、第三者（日本国外にあるも者を含みます。）に個人情報を提供しません。
              </p>
            </dd>
          </dl>
          <dl>
            <dt>5. 個人情報の開示、訂正、利用停止、削除</dt>
            <dd>
              <p>
                当社は、利用者から個人情報の開示、訂正、利用停止、削除を求められたときは、法令に定められた場合を除き、本人確認の上で、遅滞なく開示を行います。
                <br/>
                ただし、個人情報の開示につきましては、１件あたり１，１００円の手数料を頂戴しておりますので、あらかじめご了承ください。
              </p>
            </dd>
          </dl>
          <dl>
            <dt>6. お問い合わせ窓口</dt>
            <dd>
              <p>
                個人情報の取り扱いに関するお問い合わせは、下記の窓口までお願いいたします。
              </p>
              <p>
                株式会社ホットソウル　個人情報保護管理者
                <br/>
                電話番号：<a href="tel:080-1157-8650">080-1157-8650</a>
              </p>
            </dd>
          </dl>
          <dl>
            <dt>7. プライバシーポリシー</dt>
            <dd>
              <p>
                当社は、必要に応じて、本プライバシーポリシーを変更いたします。
                <br/>
                なお、本プライバシーポリシーを変更する場合は、その内容を当社サイト上で表示いたします。
              </p>
            </dd>
          </dl>
          <p class="date">2021年9月15日　制定</p>
        </div>
      </section>
    </Layout>
  )
}